* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.list_item {
  display: flex;
  align-items: center;
}

.textCountry {
  margin-left: 10px;
}

.card_item {
  display: flex;
  flex-direction: column;
}

.cardName {
  font-size: 30px;
  font-weight: 600;
}

.name_and_icon {
  display: flex;
  align-items: center;
}
.icon {
  margin-right: 10px;
}
